.load-bar {
    position: relative;
    width: 100%;
    height: 6px;
    /*background-color: #444444;*/
}
.bar {
    content: "";
    display: inline;
    position: absolute;
    width: 0;
    height: 100%;
    left: 50%;
    text-align: center;
}

.bar:nth-child(1) {
    background-color: #444444;
    animation: loading 2s linear .5s infinite;
}
.bar:nth-child(2) {
    background-color: #9060EB;
    animation: loading 2s linear 1s infinite;
}
.bar:nth-child(3) {
    background-color: #00C781;
    animation: loading 2s linear 1.5s infinite;
}

.status {
    cursor: pointer;
}

.status:hover {
    opacity: .7;
    transition: all 1s;
}

.status:active {
    opacity: 1;
    transition: all 0s;
}



@keyframes loading {
    from {left: 50%; width: 0;z-index:100;}
    33.3333% {left: 0; width: 100%;z-index: 10;}
    to {left: 0; width: 100%;}
}
